@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
/* ***************** COLOR VARIABLES  **************** */

:root {
  --primary-white: #fff;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-orange: #e0a443;
  --primary-blue: #4eb8e6;
  --primary-dark: #343A40;
  --primary-red: #ef4035;
  --primary-hover-orange: #e0a44393;
  --primary-black: #000000;
}

/* ***************** FONT TYPE  **************** */

body {
  font-family: "Roboto Condensed", sans-serif !important;
}

/* ***************** NAVBAR **************** */

.navbar {
  z-index: 2;
}

.logo {
  width: 16.25rem;
}

nav a.nav-link {
  font-size: .8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid #343A40;
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0px !important;
  height: 6rem !important;
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #e0a443;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #e0a443;
  border-top: 0.1875rem solid var(--primary-orange);
}


/* ***************** HEADER **************** */

.header-wrapper {
  position: relative;
  background: url(/portfolio/static/media/keyboard-background.11cd023d.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  /* font-family: "Roboto Condensed", sans-serif; */
  z-index: 1;
}

.main-info h1 {
  color: #4eb8e6;
  color: var(--primary-blue);
  text-transform: uppercase;
}

@media(max-width: 760px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}

.typed-text {
  font-size: 2rem;
  color: #fff;
  color: var(--primary-white)
}

.btn-main-offer {
  border: 1px solid #e0a443;
  border: 1px solid var(--primary-orange);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.625rem;
  color: #fff;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #e0a44393;
  background-color: var(--primary-hover-orange);
  color: #fff;
  color: var(--primary-white);
  transition: .2s ease-in-out;
}


/* ***************** PARTICLES **************** */

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vh;
  z-index: 1;
}

@media(max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

/* ***************** ABOUT **************** */

.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img {
  width: 18rem;
  border-radius: 50px;
  padding: .3rem;
  border: 1px solid #e0a443;
  border: 1px solid var(--primary-orange);
}

.about-heading {
  color: #e0a443;
  color: var(--primary-orange);
  text-transform: uppercase;
}

#linkedin {
  color: #2466C2;
}

#twitter {
  color: #38A1F2;
}

#medium {
  color: #000000;
  color: var(--primary-black);
}

#github {
  color: #000000;
  color: var(--primary-black);
}

@media(max-width: 468px) {
  .profile-img {
    width: 10rem;
  }
}


/* ***************** SERVICES **************** */

.services {
  text-align: center;
  padding: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-grey);
}

.services h1 {
  color: #343A40;
  color: var(--primary-dark);
  text-transform: uppercase;
}

.services .circle {
  position: relative;
  margin: 0.375rem auto;
  background: #e0a443;
  background: var(--primary-orange);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.services .icon {
  font-size: 1.4rem;
  color: #fff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); 
}

.services .box {
  height: 16rem;
  border-bottom: 0.3125rem solid #e0a443;
  border-bottom: 0.3125rem solid var(--primary-orange);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #fff;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;
}

.services .box:hover {
  background: #e0a443;
  background: var(--primary-orange);
  border-bottom: 0.3125rem solid #343A40;
  border-bottom: 0.3125rem solid var(--primary-dark);
}

.services .box:hover .icon {
  color: #343A40;
  color: var(--primary-dark);
}

.services .box:hover .circle {
  background: #fff;
  background: var(--primary-white);
}

.services .box:hover p {
  color: #fff;
  color: var(--primary-white);
}


/* ***************** EXPERIENCE **************** */

.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: #e0a443;
  color: var(--primary-orange);
}

.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid #343A40;
  border: 0.3125rem solid var(--primary-dark);
  background: #e0a443;
  background: var(--primary-orange);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: #666666;
  color: var(--primary-dark-grey);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125;
  color: #4eb8e6;
  color: var(--primary-blue);
  font-weight: 900;
}

/* ***************** MEDIA ***************** */

@media(max-width: 768px) {
  .experience {
    padding: 0.7rem;
  }
  .experience .container {
    padding-left: 0
  }
  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }
  .timeline-block-left {
    float: none;
    direction: ltr;
    width: 100%;
  }
}


/* ***************** PORTFOLIO ***************** */

.portfolio-wrapper {
  background: #f4f4f4;
  background: var(--primary-light-grey);
  padding: 3rem 0;
}

.portfolio-wrapper h1 {
  color: #e0a443;
  color: var(--primary-orange);
}

.portfolio-image {
  width: 15rem;
  height: 8rem;
  border: 1px solid #e0a443;
  border: 1px solid var(--primary-orange);
  padding: 0 0.5rem;
}

.portfolio-image-box {
  position: relative;
  margin: 1rem;
}

/* ***************** OVERFLOW BOX ***************** */

.overflow {
  position: absolute;
  opacity: 0;
  background: #000000;
  background: var(--primary-black);
  width: 15rem;
  height: 8rem;
  top: 0;
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: #e0a443;
  color: var(--primary-orange);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}


/* ***************** OVERFLOW BOX ***************** */

.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
}

.hyper-link {
  cursor: pointer;
  color: #e0a443;
  color: var(--primary-orange);
}

.hyper-link:hover {
  color: #ef4035;
  color: var(--primary-red);
}

.popupbox-wrapper {
  height: 680px;
}

@media(max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}

@media(max-height: 570px) {
  .popupbox-wrapper {
    height: 100% !important;
  }

  .portfolio-image-popupbox {
    width: 50%;
  }
}

@media(max-height: 280px) {
  .popupbox-wrapper {
    height: 90% !important;
  }

  .portfolio-image-popupbox {
    width: 30%;
  }
}


/* ***************** CONTACTS ***************** */

.contacts {
  background: #343A40;
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contacts h1 {
  color: #e0a443;
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: #f4f4f4;
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}


/* ***************** CONTACTS ***************** */

.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: #ccd1d9;
  color: var(--primary-grey);
  outline: 0 !important;
  box-shadow: none !important;
  top: 0;
  margin-bottom: 1rem;
  padding-left: 0 !important;

}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: #e0a443;
  color: var(--primary-orange);
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contacts textarea {
  height: 11.4rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line {
  top: -0.8rem;
  position: relative;
  height: 0.125rem;
  background: #e0a443;
  background: var(--primary-orange);
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  /* transition: width 0.5s ease-in-out; */
}

input.form-control {
  margin: 0.3rem 0;
}

textarea.form-control {
  margin: 0.3rem 0;
}

.success-message {
  color: #4eb8e6;
  color: var(--primary-blue);
  font-weight: 900;
}

.errors-message {
  color: #e0a443;
  color: var(--primary-orange);
  text-align: left;
}


/* ***************** media ***************** */

@media(max-width: 768) {
  .contacts {
    overflow-x: hidden;
  }
  .contacts .container {
    padding: 0 1rem;
  }
  .contacts p {
    padding: 0 0.4rem;
  }
}


/* ***************** FOOTER ***************** */


.footer {
  background: #000000;
  background: var(--primary-black);
  color: #666666;
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color: #e0a443;
  color: var(--primary-orange);
  cursor: pointer;
}

.footer p {
  margin-bottom: 0;
}

.social-links {
  padding: 1.5rem;
}


